<template>
<div>
	<div class="slider">
		<swiper :options="swiperOption">
			<swiper-slide><img src="/static/banner1.jpg" class="banner-item" alt=""></swiper-slide>
			<swiper-slide><img src="/static/banner2.jpg" class="banner-item" alt=""></swiper-slide>
			<swiper-slide><img src="/static/banner3.jpg" class="banner-item" alt=""></swiper-slide>
			<swiper-slide><img src="/static/banner4.jpg" class="banner-item" alt=""></swiper-slide>
			<div class="swiper-pagination" slot="pagination"></div>
		</swiper>
	</div>
	<div class="wrapper">
		<div class="list-head">
			<router-link :to="{path:'/songList'}">推荐歌单<mu-icon value="keyboard_arrow_right" color="red"/></router-link>
		</div>
		<mu-flexbox wrap="wrap" justify="space-between" class="box">
			<mu-flexbox-item basis="30%" class="item" :key="item.id" v-for="item in playList">
				<router-link :to="{name:'songList',params:{ id: item.id, name: item.name, coverImg: item.coverImgUrl, creator:item.creator, count:item.playCount, desc:item.description}}">
					<div class="count-bar">{{item.playCount | formatCount}}</div>
					<img :src="item.coverImgUrl+'?param=230y230'">
					<div class="item-name">{{item.name}}</div>
				</router-link>
			</mu-flexbox-item>
		</mu-flexbox>
	</div>
</div>
</template>
<script>
	import api from '../api'
	import {swiper,swiperSlide} from 'vue-awesome-swiper'
	export default {
		data(){
			return{
				swiperOption:{
					pagination:'.swiper-pagination',
					paginationClickable:true,
					autoplay:5000,
					loop:true
				},
				isloding:true,
				playList:[],
				mvList:[]
			}
		},
		components:{
			swiper,
			swiperSlide
		},
		created(){
			this.get()
		},
		methods:{
			get(){
				this.$http.get(api.getPlayListByWhere('全部','hot',0,true,6))
				.then((res)=>{
					this.isloding=false
					this.playList=res.data.playlists
				})
				.catch((error)=>{
					console.log(error)
				})
			}
		},
		filters:{
			formatCount(val){
				if(val>9999)
					return (val/10000).toFixed(0)+'万'
				else
					return val
			}
		}
	}
</script>
<style lang="less" scoped>
.slider{
	img{
		width:100%;
	}
}
.wrapper{
	a{
		color:black;
		font-size:30px;
		padding-left:10px;
	}
	.mu-icon{
		font-size:50px;
		vertical-align:middle;
	}
	overflow:auto;
}
.item{
	overflow:hidden;
	position:relative;
	img{
		width:100%;
	}
	.count-bar{
		position:absolute;
		font-size:25px;
		background-color:rgba(0,0,0,0.16);
		width:100%;
		color:white;
	}
	.item-name{
		height:3em;
		overflow:hidden;
		font-size:30px;
	}
}

</style>


