import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      children:[
      {
      	path:'rage',
      	component:require('../components/rage')
      }
      ]
    },
    {
      name:'songList',
      path:'/songList/:id',
      component:require('../components/songList')
    },
    {
      path: '*', redirect: '/rage'  // 重定向任意未匹配路径到/
    }
  ]
})



