<template>
	<div>
		<div class="loading" v-show="isloading">
			<mu-circular-progress :size="90" color="red"/>
		</div>
		<div class="head">
			<router-link :to="{path:'/rage'}"><mu-icon value="keyboard_arrow_left" color="white"/><span>返回</span></router-link>
			<div class="head-info">
				<div class="left-info">
					<span>{{playCount}}</span>
					<img   :src="coverImgUrl+'?param=300y300'">
				</div>
				<div class="right-info">
					<p>{{name}}</p>
					<p class="author">
						<mu-avatar slot="left" :src="creator.avatarUrl+'?param=50y50'" :size="60" :iconSize="20"/>
						<span>{{creator.nickname}}</span>
					</p>
				</div>
			</div>
			<div class="clear"></div>
			<div class="bg-mask" :style="{'background-image':'url(' + coverImgUrl + '?param=300y300)'}"></div>
		</div>
		<mu-list :value="value" v-show="!isloading" @change="change">
                <div v-for="(item, index) in list" @click="playAudio(item)">
                    <mu-list-item  :disableRipple="true" :title="item.name" :value="item.id" :describeText="item.ar[0].name"  class="item" :style="{'font-size':'30px'}">
                        <span slot="left" class="indexStyle">{{index + 1}}</span>
                    </mu-list-item>
                    <mu-divider inset/>
                </div>
                </mu-list>
            </div>
        </mu-list>
	</div>
</template>
<script>
	import api from '../api'
	import { mapGetters, mapMutations } from 'vuex'
	export default{
		data(){
			return{
					coverImgUrl: '../../static/default_cover.png',
				    name: '歌单标题',
				    id: 0,
				    fname: '歌单',
				    playCount: 0,
				    description: '描述描述',
				    creator: {
				        'avatarUrl': '../../static/user-default.png',
				        'nickname': '昵称'
				    },
				    list: [],
				    opacity: 0,
				    value: 0,
				    isloading: false
			}
		},
		beforeRouteEnter:(to,from,next)=>{
			next(vm=>{
				if(parseInt(to.params.id)!==parseInt(vm.id)){
					console.log(to.params.id);
					vm.get()
				}
				if(to.params.coverImg){
					vm.coverImgUrl = vm.$route.params.coverImg
			        vm.name = vm.$route.params.name
			        vm.description = vm.$route.params.desc
			        vm.playCount = vm.$route.params.count
			        vm.creator = vm.$route.params.creator
			        vm.id = vm.$route.params.id
			        console(this.id)
				}
			})
		},
		methods:{
			get () {
     		 	this.isloading = true
      			this.$http.get(api.getPlayListDetail(this.$route.params.id))
      			.then((res) => {
	        		this.list = res.data.playlist.tracks
	        		this.isloading = false
	        		console.log(this.list)
      			})
      			.catch((error) => {
        			console.log('加载歌单信息出错:' + error)
     			})
        	},
        	change (val) {
      			this.value = val
    		},
    		playAudio (song) {
		      document.getElementById('audioPlay').pause()
		      this.$store.commit('pause')
		      // this.$parent.$refs.alert.show('tess')
		      var audio = {}
		      audio.id = song.id  // id
		      audio.singer = song.ar[0].name // 演唱者
		      audio.albumPic = song.al.picUrl
		      audio.name = song.name
		      // 通过Vuex改变状态
		      this.$store.commit('addToList', audio)
		      this.$store.dispatch('getSong', audio.id)
		    }
		}
	}
</script>

<style lang="less">
	.mu-item-title {
        white-space:nowrap;
        overflow: hidden;
        font-size:30px !important;
    }
    .mu-item-text{
    	font-size:25px !important;
    	line-height:25px !important;
    }
	.loading{
		position:absolute;
		text-align: center;
		top:40%;
		width: 100%;
	}
	.head{
		position:relative;
		overflow:hidden;
		color:white;
		font-size:30px;
		vertical-align:middle;
		line-height:70px;
		//background-color:#757575;
		.mu-icon{
			font-size:70px;
			position:absolute;
		}
		span{
			margin-left:80px;
			color:white;
		}
		.bg-mask{
				position:absolute;
				top:0;
				left:0;
				width:100%;
				height:100%;
				z-index:-1;
				background-repeat: no-repeat;
        		background-size: cover;
        		filter: blur(40px);
        		-webkit-filter: blur(40px);
			}
		.head-info{
			.left-info{
				position:relative;
				width:40%;
				float:left;
				padding:20px;
				span{
					position:absolute;
					right:0;
					top:0;
					margin-right:20px;
				}
				img{
					max-width: 100%;
                    height: auto;
				}
			}
			.right-info{
				float:right;
				width:60%;
				padding:20px 20px 0 10px;
				text-align:center;
				word-wrap: break-word;
				span{
					margin-left:10px;
				}
				.mu-avatar{
					vertical-align:middle;
					margin-top:-5px;
				}

			}
		}
		.clear{
			clear:both;
		}
	}	
</style>


