<template>
  <div>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <Player v-show="songList.length > 0 && !showDetail"></Player>
  </div>
</template>

<script>
  import Player from './components/playBar'
  import { mapGetters} from 'vuex'
  export default {
    name: 'app',
    components:{
      Player
    },
    computed:{
      ...mapGetters([  
        'songList',
        'showDetail'
      ])
    }
  }
</script>

<style>

</style>



