<template>
	<div>
    	<div class="fix-bar">
    		<mu-appbar>
    		  <span class="title">网易云音乐</span>
    		  <mu-icon value="search" color="white"/>
    	    </mu-appbar>
    	    <mu-tabs :value="activeTab" @change="handleTabChange" class="view-tabs">
		        <mu-tab value="rage" title="时下流行"/>
		        <mu-tab value="songList" title="歌单"/>
		        <mu-tab value="leaderBoard" title="排行榜"/>
		        <mu-tab value="hotSinger" title="热门歌手"/>
	      	</mu-tabs>
	      	<div class="content">
	      		<keep-alive>
	      			<router-view></router-view>
	      		</keep-alive>
	      	</div>
    	</div>
	</div>
</template>
<script>

	export default{
		data(){
			return{
				activeTab:'rage'
			}
		},
		methods:{
			handleTabChange(val){
				this.activeTab=val
				this.newRouter(val)
			},
			newRouter(val){
				this.$router.push({path:'/'+val})
			},
		},
		created(){
			this.newRouter(this.activeTab)
		}
	}

</script>
<style scoped lang="less">
.fix-bar{ 
	width:100%;
	top:0;
	left:0;
	.title{
		font-size: 60px;
	}
	.appbar-search-field{
		font-size: 20px;
	}
	.mu-icon {
	    font-size: 64px;
	    cursor: inherit;
	    line-height:130xp;
	    float:right;
    }
    .mu-tabs{
    	background-color:#fff;
    	color:black;
    	.mu-tab-link {
    		color: #000000;
    		font-size:30px;
    		padding:30px
		}
		.mu-tab-active{
      		color: #ce3d3e;
    	}
    }
    overflow:scroll;
}
.mu-appbar{
	height: 120px;
	font-size: 100px;
}


</style>



